@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* 
{
margin: 0;
padding: 0;
font-family: "Quicksand", sans-serif;
}

body 
{
background: url("https://source.unsplash.com/1600x900/?nature,water");
background-repeat: no-repeat;
background-size: 100% 100%;
display: flex;
justify-content: center;
align-items: center;
flex-wrap: wrap;
height: 100vh;
width: 100vw;
}

html,body
{
font-size: 62.5%;
height: 100%;
}

html 
{
background: #eee;
}

.box 
{
width: 30vw;
height: 60vh;
box-shadow: 0 0.2rem 3rem rgba(0, 0, 0, 0.2);
background:blueviolet;
border-radius: 10px;
position: relative;
overflow: hidden;
-webkit-transform: translate3d(0, 0, 0);
        transform: translate3d(0, 0, 0);
min-width: 20rem;
min-height: 35rem;
}

.wave-one 
{
opacity: 0.3;
position: absolute;
top: 120%;
left: 50%;
background: #fff;
width: 50rem;
height: 50rem;
margin-left: -25rem;
margin-top: -25rem;
-webkit-transform-origin: 50% 48%;
        transform-origin: 50% 48%;
border-radius: 43%;
-webkit-animation: drift 3000ms infinite linear;
        animation: drift 3000ms infinite linear;
z-index: 1;
}

.wave-two 
{
opacity: 0.1;
position: absolute;
top: 110%;
left: 50%;
background: #fff;
width: 50rem;
height: 50rem;
margin-left: -25rem;
margin-top: -25rem;
-webkit-transform-origin: 50% 48%;
        transform-origin: 50% 48%;
border-radius: 43%;
-webkit-animation: drift 3000ms infinite linear;
        animation: drift 3000ms infinite linear;
z-index:3;
}

.wave-three 
{
-webkit-animation: drift 7000ms infinite linear;
        animation: drift 7000ms infinite linear;
z-index: 2 !important;
opacity: 0.2;
}


.box::after 
{
content: "";
display: block;
left: 0;
top: 0;
width: 100%;
height: 100%;
z-index: 11;
-webkit-transform: translate3d(0, 0, 0);
        transform: translate3d(0, 0, 0);
}

@-webkit-keyframes drift
{
from 
{
-webkit-transform: rotate(0deg);
        transform: rotate(0deg);
}
to 
{
-webkit-transform: rotate(360deg);
        transform: rotate(360deg);
}
}

@keyframes drift
{
from 
{
-webkit-transform: rotate(0deg);
        transform: rotate(0deg);
}
to 
{
-webkit-transform: rotate(360deg);
        transform: rotate(360deg);
}
}

.info 
{
position:absolute;
bottom: 0;
width: 100%;
height: 45%;
z-index: 4;
}

.location 
{
margin-top:1.5rem;
text-align: center;
font-weight: 800;
font-size: 3.5rem;
font-weight: 600;
}

.fa-street-view 
{
-webkit-animation: rotates 3s linear infinite alternate;
        animation: rotates 3s linear infinite alternate;
font-size: 30px;
}

@-webkit-keyframes rotates 
{
from 
{
-webkit-transform: translateX(-0.5rem);
        transform: translateX(-0.5rem);
}
to 
{
-webkit-transform: translateX(0.5rem);
        transform: translateX(0.5rem);
}
}

@keyframes rotates 
{
from 
{
-webkit-transform: translateX(-0.5rem);
        transform: translateX(-0.5rem);
}
to 
{
-webkit-transform: translateX(0.5rem);
        transform: translateX(0.5rem);
}
}

#date 
{
text-align: center;
margin-top: 0.5rem;
color: #57606f;
font-size: 1.5rem;
font-weight: 300;
text-transform: uppercase;
}

.temp 
{
margin-top: 2.5rem;
text-align: center;
font-size: 3rem;
}

.tempmin_max 
{
text-align: center;
margin-top: 0.8rem;
font-weight: 800;
font-size: 1.6rem;
color: #070707;
}

#weathercon 
{
height: 55%;
display: flex;
justify-content: center;
align-items: center;
font-size: 3rem;
}

#weathercon .fas 
{
font-size: 6rem;
-webkit-animation: fas-anime 3s linear infinite alternate;
        animation: fas-anime 3s linear infinite alternate;
}

@-webkit-keyframes fas-anime 
{
from 
{
-webkit-transform: scale(1.1);
        transform: scale(1.1);
}
to 
{
-webkit-transform: scale(1.5);
        transform: scale(1.5)
}
}

@keyframes fas-anime 
{
from 
{
-webkit-transform: scale(1.1);
        transform: scale(1.1);
}
to 
{
-webkit-transform: scale(1.5);
        transform: scale(1.5)
}
}

@media (max-width: 600px) 
{
.box 
{
width: 90vw;
height: 80vh;
}

.wave 
{
top: 85%;
}

#weathercon 
{
font-size: 5em;
}

.info {
font-size: 1.5em;
}
}

@media (max-height: 500px) 
{
.box 
{
height: 80vh;
}

.wave 
{
top: 115%;
}
}

body > span 
{
width: 100vw;
text-align: center;
color: grey;
}
.inputData {
width: 100%;
height: 10vh;
display: flex;
justify-content: center;
align-items: center;
}

.inputFeild {
width: 50%;
margin: auto;
margin-top:10px;
border-radius: 2rem;
padding: 1rem 1rem;
outline: none;
font-size: 2rem;
font-weight: bold;
}
.inputFeild:hover{
    background-color:lightseagreen;
}
.errormsg {
text-align: center;
font-size: 3rem;
margin: 2rem auto;
font-weight:bold;
}


.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

